import React from 'react'
import { connect } from 'react-redux'

import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

class WindowContent extends React.Component {

	constructor(props) {

	    super(props);

	    this.state = { 
	    	content: null,
	    	style: null 
	    };

	    console.log(this.props.menuOpen)

	}



	render() {

		if(this.props.menuOpen) {
	    	var style = {
				transition: 'marginLeft 6',
				marginLeft: 255 ,
				padding: 20
			}
		}else{
			var style = {
				transition: 'marginLeft 6',
				marginLeft: 0,
				padding: 20
			}
		}

		switch(this.props.activePage) {
			case 1:
				  content = <div style={style}> Page 1 </div>
				  break;
			case 2:
				  content = <div> Page 2 </div>
				  break;
			default:
				  content = <div> Page Load Failed </div>		
		}

		return (
			<div>
				{this.state.content}
			</div>
		)
	}
}


function mapStateToProps(state) {
	
	//map state variables to the component's state 
 	return {
 		activePage: state.activePage,
 		menuOpen: state.menuOpen
 	}
}


export default connect(
  mapStateToProps,
  {} //add importing action functions here
)(WindowContent);


